.tabPrefixButton {
  cursor: pointer;
  background: transparent;
  color: inherit;
  border: none;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 600;
  margin-left: 10px;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}

.tabSettingsModalOverlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10000;
  align-items: center;
  justify-content: center;

  &.open {
    display: flex;
  }
}

.tabSettingsModal {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  max-width: 800px;
  width: 90%;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.modalHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #e0e0e0;

  h2 {
    margin: 0;
    font-size: 20px;
    font-weight: 600;
  }

  .closeButton {
    background: none;
    border: none;
    font-size: 32px;
    line-height: 1;
    cursor: pointer;
    color: #666;
    padding: 0;
    width: 32px;
    height: 32px;

    &:hover {
      color: #333;
    }
  }
}

.modalBody {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.modalTabs {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  border-bottom: 2px solid #e0e0e0;
}

.modalTabButton {
  background: none;
  border: none;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  color: #666;
  border-bottom: 3px solid transparent;
  margin-bottom: -2px;
  transition: all 0.2s;

  &:hover {
    color: #337ab7;
  }

  &.active {
    color: #337ab7;
    border-bottom-color: #337ab7;
  }
}

.modalTabContent {
  display: none;

  &.active {
    display: block;
  }
}

.settingsSection {
  margin-bottom: 20px;
}

.settingsLabel {
  font-weight: 600;
  margin-bottom: 8px;
  font-size: 14px;
  display: block;
}

.settingsHelp {
  font-size: 12px;
  color: #666;
  margin-bottom: 10px;
  font-style: italic;
}

.settingsSelect {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;

  &:focus {
    outline: none;
    border-color: #337ab7;
    box-shadow: 0 0 0 2px rgba(51, 122, 183, 0.1);
  }
}

.prefixTextarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-family: monospace;
  font-size: 13px;
  resize: vertical;
  box-sizing: border-box;
  min-height: 200px;

  &:focus {
    outline: none;
    border-color: #337ab7;
    box-shadow: 0 0 0 2px rgba(51, 122, 183, 0.1);
  }
}

.checkboxContainer {
  margin-top: 15px;
  display: flex;
  align-items: center;
  gap: 8px;

  input[type="checkbox"] {
    width: 18px;
    height: 18px;
    cursor: pointer;
  }

  label {
    cursor: pointer;
    font-size: 14px;
    user-select: none;
    margin: 0;
  }
}

.modalFooter {
  padding: 15px 20px;
  border-top: 1px solid #e0e0e0;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.primaryButton,
.secondaryButton {
  padding: 8px 20px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.primaryButton {
  background: #337ab7;
  color: white;

  &:hover {
    background: #286090;
  }
}

.secondaryButton {
  background: #e0e0e0;
  color: #333;

  &:hover {
    background: #d0d0d0;
  }
}
